/* File: src/App.css */
:root {
  --primary: #ffa116;
  --secondary: #1a1a1a;
  --accent: #007bff;
  --light: #f8f9fa;
  --dark: #2d2d2d;
  --success: #28a745;
  --danger: #dc3545;
  --warning: #ffc107;
  --info: #17a2b8;
  --gray: #6c757d;
  --white: #ffffff;
  --card-bg: #ffffff;
  --body-bg: #f5f5f5;
  --text-primary: #212529;
  --text-secondary: #495057;
  --border-radius: 8px;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background-color: var(--body-bg);
  color: var(--text-primary);
  transition: var(--transition);
  line-height: 1.6;
}

.App {
  min-height: 100vh;
}

.app-header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  margin-bottom: 30px;
  border-bottom: 1px solid var(--light);
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo h1 {
  font-size: 1.8rem;
  color: var(--primary);
}

.logo-icon {
  font-size: 2rem;
  color: var(--primary);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.user-input-section {
  background-color: var(--card-bg);
  padding: 20px;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: 30px;
}

.input-group {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

input {
  flex: 1;
  padding: 10px 15px;
  border: 1px solid var(--light);
  border-radius: var(--border-radius);
  background-color: var(--body-bg);
  color: var(--text-primary);
  font-size: 1rem;
}

button {
  padding: 10px 20px;
  background-color: var(--primary);
  color: var(--secondary);
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-weight: 600;
  transition: var(--transition);
}

button:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background-color: var(--card-bg);
  padding: 20px;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  text-align: center;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary);
  margin: 10px 0;
}

.stat-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.progress-section {
  background-color: var(--card-bg);
  padding: 20px;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: 30px;
}

.progress-bar {
  height: 10px;
  background-color: var(--light);
  border-radius: 5px;
  margin: 15px 0;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background-color: var(--primary);
  border-radius: 5px;
  transition: width 0.5s ease;
}

.problems-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.problems-table th,
.problems-table td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid var(--light);
}

.problems-table th {
  background-color: var(--light);
  color: var(--text-secondary);
  font-weight: 600;
}

.problems-table tr:hover {
  background-color: rgba(255, 161, 22, 0.05);
}

.loading {
  text-align: center;
  padding: 20px;
}

.spinner {
  border: 4px solid var(--light);
  border-top: 4px solid var(--primary);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 0 auto;
}

.error-message {
  background-color: rgba(220, 53, 69, 0.1);
  color: var(--danger);
  padding: 10px;
  border-radius: var(--border-radius);
  margin-top: 10px;
  margin-bottom: 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media (max-width: 768px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .input-group {
    flex-direction: column;
  }
}